<template>
    <div>
        <text class="message">Now, let's use Vue.js to build your Weex app.</text>
        <text @click="tel"></text>
        <!--<web src="https://saxo.fdzq.com/login/" @error="onError" style="height:500px;"></web>-->
        <web src="https://www.baidu.com/" @error="onError" style="height:500px;border: 1px solid #000;"></web>
    </div>
</template>

<script>
const modal = weex.requireModule('modal');

export default {
  methods: {
    tel() {
      try {
        weex.requireModule('tel').call('18521092102');
      } catch ({ message }) {
        modal.alert({ message });
      }
    },
    onError(e) {
      modal.alert(e);
    },
  },
};
</script>



